import ReactDOM from 'react-dom';
import '../app/App'; // for the CSS
import { ReporterFromSlaveToMasterTestsAreDemoInner, ReporterFromSlaveToMasterTestsAreDemoInnerName } from '../appTestingTadItself/reporterFromSlaveToMaster/ReporterFromSlaveToMasterTestsAreDemoInner';
import { SourceCodeTestsAreDemoInner, SourceCodeTestsAreDemoInnerName } from '../appTestingTadItself/sourceCode/SourceCodeTestsAreDemoInner';
import { tad, TestsAreDemoAppWrapper } from '../lib';

const innerTests = {
    [ReporterFromSlaveToMasterTestsAreDemoInnerName]: ReporterFromSlaveToMasterTestsAreDemoInner,
    [SourceCodeTestsAreDemoInnerName]: SourceCodeTestsAreDemoInner
}

const search = new URLSearchParams(window.location.search);
const innerTestToRunString = search.get("innerTestToRun");
// @ts-ignore
const innerTestToRun = innerTests[innerTestToRunString];
if (!innerTestToRun) {
    throw new Error("Couldn't find innerTestToRun; the 'innerTestToRun' query string was: " + innerTestToRunString);
}

// This app is (re)loaded by every test. It is instructed to run a particular test (that we call "inner" test).
// We render <TestsAreDemoAppWrapper /> to test also the "if" in it's render() function. But otherwise, this app
// always render TestsAreDemoSlave. It doesn't ever render neither the "original" component, nor TestsAreDemoMaster
ReactDOM.render(<TestsAreDemoAppWrapper app={<p>Not meant to be ran in non-TAD mode</p>} importTestsCallback={async () => {
    tad.addTests(innerTestToRun)
}} />, document.getElementById('root'));